/* -----------------------------------------------------------------------------
  Component: Brand and logo

  Style for the element containing the logo in the header.
----------------------------------------------------------------------------- */
.navbar-brand {
  display: flex;
  justify-content: flex-end;
}

.navbar-brand .logo-link.web {
  padding-bottom: .3rem;
}

.navbar-brand .logo-link.docs {
  margin-left: 5px;
  line-height: 2.15;
}

.navbar-brand .logo-link.docs:hover {
  text-decoration: none;
}

/* Light theme -------------------------------------------------------------- */
body .navbar-brand .logo-link.docs {
  color: #292929;
}

body .navbar-brand .logo-link .logo-light {
  display: inline-block;
}

body .navbar-brand .logo-link .logo-dark {
  display: none;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme .navbar-brand .logo-link.docs {
  color: #fff;
}

.dark-theme .navbar-brand .logo-link .logo-light {
  display: none;
}

.dark-theme .navbar-brand .logo-link .logo-dark {
  display: inline-block;
}

/* -----------------------------------------------------------------------------
  Component: Mode switch

  Style for the component that switches the theme style to light or dark mmode.
----------------------------------------------------------------------------- */
#light-dark-switch {
  display: flex;
  flex-direction: row;
  padding-bottom: 8px;
}

#light-dark-switch > span:first-child {
  margin-left: 8px;
}

#light-dark-switch .switch-icon {
  width: 20px;
  height: 20px;
  margin-left: 5px;
  margin-right: 5px;
  display: inline-block;
  background-size: contain;
  background-position: center;
}

#mode-switch {
  box-sizing: content-box;
  position: relative;
  top: 1px;
  display: flex;
  flex-direction: row;
  width: 26px;
  height: 16px;
  padding: 0;
  margin: 0 5px;
  border: 1px solid;
  border-radius: 25px;
}

#mode-switch.dark {
  justify-content: flex-end;
}

#mode-switch.dark span {
  left: calc(100% - 16px);
}

#mode-switch span {
  position: absolute;
  left: 0;
  display: inline-block;
  width: 16px;
  height: 16px;
  content: "";
  border-radius: 50%;
  transition: left ease-in .075s;
  background-color: #fff;
}

/* Light theme -------------------------------------------------------------- */
body #light-dark-switch {
  color: #292929;
}

body #light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun-lt");
}

body #light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon-lt");
}

body #mode-switch {
  background-color: #3585f9;
  border-color: #3585f9;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme #light-dark-switch {
  color: #fff;
}

.dark-theme #light-dark-switch .switch-icon.icon-sun {
  background: url("../../images/icons/svg-icons.svg#sun-dt");
}

.dark-theme #light-dark-switch .switch-icon.icon-moon {
  background: url("../../images/icons/svg-icons.svg#moon-dt");
}

.dark-theme #mode-switch {
  background-color: #499cfe;
  border-color: #499cfe;
}

/* -----------------------------------------------------------------------------
  Component: Search box

  Style for the searchbox in general.
----------------------------------------------------------------------------- */
.search-box {
  border: 2px solid;
  border-radius: 10px;
}

.search-box .search-form {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  height: 46px;
}

.search-box .search-form .form-control {
  position: relative;
  z-index: 1;
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  border: none;
}

.search-box .search-form .btn-search {
  position: absolute;
  right: 0;
  z-index: 10;
  width: 46px;
  height: 46px;
  padding: 5px;
  background-repeat: no-repeat;
  background-size: 41px 41px;
  background-position: center;
  border: none;
}

/* Light theme -------------------------------------------------------------- */
body .search-box {
  background-color: #ebf2fb;
  border-color: #c1ccd9;
}

body .search-box .form-control {
  background-color: #ebf2fb;
}

body .search-box .btn-search {
  background: url("../../images/icons/svg-icons.svg#search-lt");
}

body .search-box ::placeholder {
  color: #a6b5c8;
}

/* Dark- theme -------------------------------------------------------------- */
.dark-theme .search-box {
  background-color: #343741;
  border-color: #fff;
}

.dark-theme .search-box .form-control {
  background-color: #343741;
}

.dark-theme .search-box .btn-search {
  background: url("../../images/icons/svg-icons.svg#search-dt");
}

.dark-theme .search-box ::placeholder {
  color: #a6b5c8;
}

/* -----------------------------------------------------------------------------
