}

.dark-theme footer .nav-link:hover {
  color: #6ca2f0;
}

body {
  margin-top: 215px;
}

main {
  padding-left: 15px;
  padding-right: 15px;
  min-height: calc(100vh - 215px - 70px);
  padding-top: 60px;
}

main h1 {
  width: 0;
  height: 0;
  overflow: hidden;
}

main .toctree-wrapper ul {
  padding: 0;
  list-style: none;
}

main .toctree-wrapper > ul {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}

main .toctree-wrapper > ul .toctree-l1 {
  margin-left: 70px;
  width: calc(33.333% - 70px);
  margin-bottom: 60px;
  position: relative;
  min-height: 133px;
  border-left: 3px solid;
}

main .toctree-wrapper > ul .toctree-l1.collapsible .toggle {
  display: inline-block;
  left: -3px;
  font-size: 18px;
  font-weight: 700;
  padding: 10px 0;
}

main .toctree-wrapper > ul .toctree-l1.collapsible .toggle::after {
  content: '';
  display: inline-block;
  width: 24px;
  height: 12px;
  background-repeat: no-repeat;
  background-size: 20px 15px;
  background-position: center;
  margin-left: 7px;
  transform: rotateX(180deg);
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed ul {
  position: relative;
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed .toggle::after {
  transform: rotateX(0);
}

main .toctree-wrapper > ul .toctree-l1.collapsible.collapsed .toctree-l2:nth-of-type(n+4) {
  display: none;
}

main .toctree-wrapper > ul .toctree-l1 a {
  border-left: 3px solid;
  position: relative;
  left: -3px;
  padding-left: 15px;
  display: inline-block;
  line-height: 32px;
  font-weight: 400;
}

main .toctree-wrapper > ul .toctree-l1 a:hover {
  text-decoration: none;
}

main .toctree-wrapper > ul .toctree-l1 > a {
  font-size: 22px;
  line-height: 22px;
  font-weight: 700;
  padding-bottom: 15px;
}

main .toctree-wrapper > ul .toctree-l2 {
  position: relative;
}

.loading .toctree-l1 {
  height: 133px;
  overflow-y: hidden;
}

